const limits = {
  jonas: 1500,
  matilda: 100,
};

const add = function (value, description, user = 'jonas') {
  user = user.toLowerCase();

  const lim = limits[user] ? limits[user] : 0;

  if (value <= lim) {
    budget.push({ value: -value, description, user});
  }
};
add(10, 'Pizza üçï');
add(100, 'Going to movies üçø', 'Matilda');
add(200, 'Stuff', 'Jay');
console.log(budget);

const check = function () {
  for (const el of budget) {
    const lim = limits[el.user] ? limits[el.user] : 0;

    if (el.value < -lim) {
      el.flag = 'limit';
    }
  }
};
check();

console.log(budget);

const bigExpenses = function (limit) {
  const output = '';
  for (const el of budget) {
    if (el.value <= -limit) {
      output += el.description.slice(-2) + ' / '; // Emojis are 2 chars
    }
  }
  output = output.slice(0, -2); // Remove last '/ '
  console.log(output);
};
